<template>

    <div id="main">

        <div class="header">
            <h1 class="title">Movies</h1>
        </div>

        <router-link to="/movie/add" tag="button" class="button is-primary">Add Movie</router-link> 

        <div class="content">
            <table class="">
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Movie Title</th>
                        <th>Movie Length</th>
                        <th>Release Date</th>
                        <th>
                           
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="movie in movies" :key="movie.id">
                        <td>{{ movie.id }}</td>
                        <td><a @click="movieDetail(movie.id)">{{ movie.movieTitle }}</a></td>
                        <td>{{ movie.movieLength }}</td>
                        <td>{{ movie.releaseDate }}</td>
                        <td> <button @click="deleteMovie(movie.id)" class="button is-secondary"> Delete Movie</button> </td>
                    </tr>
                </tbody>
            </table>

        </div>
    </div>

</template>

<script>
export default {
    data: () => ({
        movies: []
    }),
    methods: {
        movieDetail(movieId) {
            this.$router.push('movie/' + movieId);
        },
        async deleteMovie(movieId) {
            await this.$http.delete(`http://localhost:8080/api/movies/${movieId}`)
            const { data } = await this.$http.get('http://localhost:8080/api/movies');
            this.movies = data;
        }
    },
    async mounted() {
        const { data } = await this.$http.get('http://localhost:8080/api/movies');
        this.movies = data;
    }
}
</script>

<style scoped>
    button {
        float: right;
    }
</style>
